import React from "react";
import "./skill-card.css";
import { EN, FA } from "../../Data/lang-data";

function SkillCard(props) {
    return (
        <>
            <div
                className="skill-card"
                dir={props.langToggle ? EN.direction : FA.direction}
            >
                <div className="card-wrapper">
                    <div className="skill-icon">{props.skillIcon}</div>
                    <div className="skill-title">
                        <h1>{props.title}</h1>
                    </div>
                    <div
                        className="skill-desc"
                        dir={props.langToggle ? EN.direction : FA.direction}
                    >
                        <p
                       
                        >{props.skillDesc}</p>
                    </div>
                    <div className="skill-libs">
                        {props.skillLibs.map((item) => {
                            return (
                                <div key={item} className="skill-lib">
                                    {item}
                                </div>
                            );
                        })}
                    </div>
                </div>
            </div>
        </>
    );
}

export default SkillCard;
